import React from "react";
import { BrowserRouter, Route, Switch, Redirect } from "react-router-dom";
import { Provider } from "react-redux";
import Boletines from "./Boletines/Boletines";
import Editor from "./PostArticle/PostArticle";
import PostBoletin from "./PostBoletin/PostBoletin";
import Boletin from "./Boletin/Boletin";
import Categorias from "./Categorias/Categorias";
import PostCategory from "./PostCategory/PostCategory";
import Article from "./Article/Article";
import store from "./store";

const App = () => (
  <BrowserRouter>
    <Provider store={store}>
      <Switch>
        <Redirect from="/" exact to="/boletines" />
        <Route
          path="/boletines"
          exact
          component={() => <Boletines activeTab={1} />}
        />
        <Route path="/categorias" exact component={Categorias} />
        <Route path="/post-article/:boletin" component={Editor} />
        <Route path="/boletines/post" exact component={PostBoletin} />
        <Route path="/boletines/:id" exact component={Boletin} />
        <Route path="/categorias/post" exact component={PostCategory} />
        <Route path="/article/:id" exact component={Article} />
      </Switch>
    </Provider>
  </BrowserRouter>
);

/*      <Route path="/categorias" exact component={} />
      <Route path="/boletines/:id" exact component={} />
      <Route path="/boletines/upload" exact component={} />
      <Route path="/categorias/upload" exact component={} />
      <Route path="/article/upload" exact component={}/>
      <Route path="/article/:id" exact component={}/>*/

export default App;



