import { combineReducers } from "redux";
import {
  GET_BOLETINES,
  GET_ARTICLES,
  GET_CATEGORIES,
  CREATE_BOLETIN,
  CREATE_ARTICLE,
  CREATE_CATEGORY,
  SUCCESS,
  UPLOAD_AUDIO
} from "./actions";

const boletines = (state = [], action) => {
  switch (action.type) {
    case GET_BOLETINES:
      return action.payload;
      break;
    default:
      return state;
  }
};

const articles = (state = [], action) => {
  switch (action.type) {
    case GET_ARTICLES:
      return action.payload;
      break;
    default:
      return state;
  }
};

const categories = (state = [], action) => {
  switch (action.type) {
    case GET_CATEGORIES:
      return action.payload;
      break;
    default:
      return state;
  }
};

const uploading = (state = "", action) => {
  if (action.type === UPLOAD_AUDIO) {
    return action.payload;
  } else {
    return state;
  }
};

const success = (state = false, action) => {
  switch (action.type) {
    case SUCCESS:
      return action.payload;
      break;
    default:
      return state;
  }
};

const rootReducer = combineReducers({
  boletines,
  articles,
  categories,
  success,
  uploading
});

export default rootReducer;



