import React, { Component } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import Toolbar from "../Toolbar/Toolbar";
import trash from "../assets/trash.png";
import Delete from "../Common/Delete";
import Footer from "../Footer/Footer.js";
import {
  fetchBoletines,
  fetchArticles,
  fetchCategories,
  putBoletin,
  deleteBoletin
} from "../actionCreators";
import "./Boletines.css";

const SectionTitle = props => {
  return <div className="section-title">{props.sectionTitle}</div>;
};

const ListContainer = props => {
  return <div className="list-container" />;
};

const BoletinRow = props => {
  return (
    <Link to={`/boletines/${props.id}`} className="boletin-row">
      <Link
        to="#"
        className="trash"
        onClick={props.delete(props.id, props.title)}
      >
        <img src={trash} />
      </Link>
      <div>
        <h2>
          {props.title.length > 30 ? (
            `${props.title.slice(0, 30)}…`
          ) : (
            props.title
          )}
        </h2>
        <span>{props.created_at}</span>
        <br />
        <span>Artículos: {props.artCount}</span>
      </div>
      <div
        style={{
          display: "flex",
          justifyContent: "space-between",
          width: "47%"
        }}
      >
        <Link className="add-article-link" to={`/post-article/${props.id}`}>
          <button className="add-article-btn">+ artículo</button>
        </Link>
        {props.published ? (
          <div className="published-boletin">PUBLICADO</div>
        ) : (
          <Link
            id={props.id}
            className="publish-btn"
            onClick={event => {
              props.publishBoletinNow(event.target.id);
            }}
            to="#"
          >
            publicar
          </Link>
        )}
      </div>
    </Link>
  );
};

/*  */

class Boletines extends Component {
  state = {
    idToPublish: 0,
    delete: { bool: false }
  };
  componentDidMount() {
    this.props.getBoletines();
    this.props.getArticles();
  }

  handlePublish = targetId => {
    this.setState(
      {
        idToPublish: targetId
      },
      () => {
        this.props.publishBoletin(this.state.idToPublish);
      }
    );
  };

  deletePrompt = (id, title) => {
    return () => {
      this.setState({
        delete: {
          bool: true,
          id: id,
          title: title
        }
      });
    };
  };

  meto = id => {
    return () => {
      this.props.deleteBoletin(id);
      this.setState({ delete: { bool: false } });
    };
  };

  closeDialog = () => {
    this.setState({ delete: { bool: false } });
  };

  render() {
    return (
      <div
        style={{ minHeight: "100%", paddingBottom: 120, position: "relative" }}
      >
        {this.state.delete.bool ? (
          <Delete
            kind="boletin"
            title={this.state.delete.title}
            borrar={this.meto}
            id={this.state.delete.id}
            closeDialog={this.closeDialog}
          />
        ) : null}
        <Toolbar activeTab={this.props.activeTab} />
        <SectionTitle
          sectionTitle="Boletines"
          style={{ backgroundColor: "#e3e3ea" }}
        />
        <div className="list-container">
          {this.props.allBoletines.length > 0 ? (
            this.props.allBoletines.map(bol => (
              <BoletinRow
                title={bol.title}
                created_at={bol.created_at}
                artCount={bol.artCount}
                id={bol._id}
                key={bol._id}
                published={bol.published}
                publishBoletinNow={this.handlePublish}
                delete={this.deletePrompt}
              />
            ))
          ) : (
            <div
              style={{
                textAlign: "center",
                padding: "15%",
                fontSize: "2em",
                color: "#9B938B"
              }}
            >
              No hay boletines creados
            </div>
          )}
        </div>
        <div className="add-boletin-container">
          <Link to="/boletines/post" className="add-boletin-btn">
            <button>agregar boletín</button>
          </Link>
        </div>
        <Footer />
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    allBoletines: state.boletines.map(bol => {
      var artCount = 0;

      if (state.articles.length > 0) {
        state.articles.forEach(art => {
          if (art.boletin === bol._id) {
            artCount += 1;
          }
        });
      }

      console.log(state.articles);

      return Object.assign({}, bol, { artCount });
    }),
    allArticles: state.articles
  };
};

const mapDispatchToProps = dispatch => ({
  getBoletines() {
    dispatch(fetchBoletines());
  },
  getArticles() {
    dispatch(fetchArticles());
  },
  getCategories() {
    dispatch(fetchCategories());
  },
  publishBoletin(id) {
    dispatch(putBoletin(id));
  },
  deleteBoletin(id, changeState) {
    dispatch(deleteBoletin(id));
  }
});

export default connect(mapStateToProps, mapDispatchToProps)(Boletines);



