import React, { Component } from "react";
import { connect } from "react-redux";
import Toolbar from "../Toolbar/Toolbar";
import { Redirect } from "react-router";
import { postBoletin } from "../actionCreators";
import Footer from "../Footer/Footer";
import "./PostBoletin.css";

class PostBoletin extends Component {
  state = {
    msg: "",
    title: "",
    allowSubmit: false
  };

  checkInputs = () => {
    if (this.state.title.length > 0 && this.state.msg.length > 0) {
      this.setState({
        allowSubmit: true
      });
    } else {
      this.setState({
        allowSubmit: false
      });
    }
  };

  msgInput = event => {
    this.setState(
      {
        msg: event.target.value
      },
      () => {
        this.checkInputs();
      }
    );
  };

  titleInput = event => {
    this.setState(
      {
        title: event.target.value
      },
      () => {
        this.checkInputs();
      }
    );
  };

  submitBoletin = () => {
    var reqBody = {
      title: this.state.title,
      msg: this.state.msg
    };

    this.props.createBoletin(reqBody);
  };

  render() {
    if (this.props.success) {
      return <Redirect to="/boletines" />;
    }
    return (
      <div
        style={{ minHeight: "100%", paddingBottom: 120, position: "relative" }}>
        <Toolbar activeTab={1} />
        <div style={{ padding: "0px 15%" }}>
          <div className="route-title">Crear boletín</div>
          <input
            type="text"
            className="boletin-input"
            placeholder="Nombre del boletín"
            value={this.state.title}
            onChange={this.titleInput}
          />
          <input
            type="text"
            className="boletin-input"
            placeholder="Mensaje a usuarios"
            value={this.state.msg}
            onChange={this.msgInput}
          />
          <div>
            <button
              className={
                this.state.allowSubmit ? "submit-boletin" : "disabled-btn"
              }
              onClick={this.state.allowSubmit ? this.submitBoletin : null}
            >
              Crear
            </button>
          </div>
        </div>
        <Footer />
      </div>
    );
  }
}

const mapStateToProps = state => ({
  success: state.success
});

const mapDispatchToProps = dispatch => ({
  createBoletin(reqBody) {
    dispatch(postBoletin(reqBody));
  }
});

export default connect(mapStateToProps, mapDispatchToProps)(PostBoletin);



