import React, { Component } from "react";
import { Link } from "react-router-dom";
import "./Toolbar.css";

const Toolbar = props => {
  return (
    <div className="toolbar">
      <Link
        className={
          props.activeTab === 1 ? "activeTab header-link" : "header-link"
        }
        to="/boletines"
      >
        <button className="header-btn">Boletines</button>
      </Link>
      <Link
        className={
          props.activeTab === 2 ? "activeTab header-link" : "header-link"
        }
        to="/categorias"
      >
        <button className="header-btn">Categorías</button>
      </Link>
    </div>
  );
};

export default Toolbar;



