import React, { Component } from "react";
import "./Delete.css";

const Delete = props => {
  return (
    <div className="delete-film">
      <div>
        Eliminar {props.kind}{" "}
        {props.title.length > 20 ? (
          `${props.title.slice(0, 20)}…`
        ) : (
          props.title
        )}{" "}
        <br />
        <button onClick={props.borrar(props.id)}> BORRAR</button>
        <button id="nojoda" onClick={props.closeDialog}>
          NO
        </button>
      </div>
    </div>
  );
};

export default Delete;



