import React, { Component } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import Toolbar from "../Toolbar/Toolbar";
import trash from "../assets/trash.png";
import Delete from "../Common/Delete";
import Footer from "../Footer/Footer.js";
import axios from "axios";
import {
  fetchArticles,
  fetchBoletines,
  fetchCategories,
  deleteArticle,
  setUploading
} from "../actionCreators";
import "./Boletin.css";

//http:localhost:3001/api/article/audio/${props.id}
const ArticleRow = props => {
  const handleSubmitAudio = event => {
    event.preventDefault();
    var formData = new FormData();

    var file = event.target.sampleFile.files[0];

    var aidi = event.target.sampleFile.id;

    console.dir(event.target.sampleFile);

    console.log(aidi);

    formData.append("nojoda", file);

    axios
      .post(`https://boletines.online/api/article/audio/${aidi}`, formData, {
        headers: {
          "Content-Type": "multipart/form-data"
        }
      })
      .then(() => {
        props.getArticles();
        props.setUploading("");
      });

    props.setUploading(aidi);
    //dispatch something to show it's loading
  };

  class Audio extends Component {
    state = {
      file: ""
    };
    render() {
      if (props.audioPath) {
        return (
          <audio
            controls
            style={{
              width: "100px",
              alignSelf: "center",
              marginRight: "130px"
            }}
          >
            <source
              src={`https://www.boletines.online/api/article/audio/${props.id}`}
            />
            Your browser does not support the audio element.
          </audio>
        );
      } else if (props.uploading_audio === props.id) {
        return (
          <div
            style={{
              width: "100px",
              textAlign: "center",
              marginRight: "130px",
              paddingTop: "35px"
            }}
          >
            Subiendo...
          </div>
        );
      } else {
        return (
          <div className="input-box">
            <form onSubmit={handleSubmitAudio}>
              <label id={`${props.id}label`} htmlFor={props.id}>
                Cargar audio
              </label>
              <input
                className="input-el"
                type="file"
                id={props.id}
                name="sampleFile"
                onChange={event => {
                  var upload = document.getElementById(`${props.id}upload`);
                  var label = document.getElementById(`${props.id}label`);
                  var span = document.getElementById(`${props.id}span`);
                  this.setState({
                    file: event.target.files[0].name.slice(0, 11)
                  });
                  span.style.display = "block";

                  label.style.display = "none";
                  upload.style.display = "inline-block";
                }}
              />

              <span
                style={{ display: "none" }}
                id={`${props.id}span`}
                className="file-span"
              >
                {this.state.file}…
              </span>
              <input
                style={{ display: "none" }}
                id={`${props.id}upload`}
                type="submit"
                value="Upload"
                className="upload-audio"
              />
            </form>
          </div>
        );
      }
    }
  }
  let Compo = null;

  let Loading = () => {
    return;
  };

  if (props.uploading_audio) {
    Compo = <Loading />;
  } else {
    Compo = <Audio />;
  }
  return (
    <div className="article-row">
      <Link to={`/article/${props.id}`} className="article-row-link">
        <Link
          to="#"
          className="trash"
          onClick={props.delete(props.id, props.title)}
        >
          <img src={trash} />
        </Link>
        <div>
          <h3>
            {props.title.length > 30 ? (
              `${props.title.slice(0, 30)}…`
            ) : (
              props.title
            )}
          </h3>

          <span className="article-span">{props.created_at}</span>
          <br />
          <span className="article-span">Categoría: {props.category}</span>
        </div>
      </Link>
      <Audio />
    </div>
  );
};

class Boletin extends Component {
  state = {
    delete: { bool: false }
  };
  componentDidMount() {
    this.props.getBoletines();
    this.props.getArticles();
    this.props.getCategories();
  }
  deletePrompt = (id, title) => {
    return () => {
      this.setState({
        delete: {
          bool: true,
          id: id,
          title: title
        }
      });
    };
  };

  meto = id => {
    return () => {
      this.props.deleteArticle(id);
      this.setState({ delete: { bool: false } });
    };
  };

  closeDialog = () => {
    this.setState({ delete: { bool: false } });
  };

  render() {
    if (!this.props.matchedBoletin) {
      return <div>Este boletin no existe</div>;
    }
    return (
      <div
        style={{ minHeight: "100%", paddingBottom: 120, position: "relative" }}
      >
        {this.state.delete.bool ? (
          <Delete
            kind="articulo"
            title={this.state.delete.title}
            id={this.state.delete.id}
            borrar={this.meto}
            closeDialog={this.closeDialog}
          />
        ) : null}
        <Toolbar activeTab={1} />
        <div style={{ padding: "0px 15%" }} />
        <div
          style={{
            fontSize: "1.6em",
            fontWeight: "bold",
            padding: "10px 15%"
          }}
        >
          Noticias en boletín{" "}
          {this.props.matchedBoletin ? this.props.matchedBoletin.title : ""}
        </div>
        <div className="article-list-container">
          {this.props.boletinArticles.length > 0 ? (
            this.props.boletinArticles.map(art => (
              <ArticleRow
                getArticles={this.props.getArticles}
                audioPath={art.audioPath}
                uploading_audio={this.props.uploading_audio}
                setUploading={this.props.setUploading}
                delete={this.deletePrompt}
                id={art._id}
                title={art.title}
                created_at={art.created_at}
                category={
                  art.category !== "0" && this.props.categories.length > 0 ? (
                    this.props.categories.find(
                      cat => cat._id.toString() === art.category
                    ).title
                  ) : (
                    "ninguna"
                  )
                }
              />
            ))
          ) : (
            <div
              style={{
                textAlign: "center",
                padding: "15%",
                fontSize: "2em",
                color: "#9B938B"
              }}
            >
              No hay artículos en este boletín
            </div>
          )}
        </div>
        <div className="add-boletin-container">
          <Link
            to={`/post-article/${this.props.match.params.id}`}
            className="add-boletin-btn"
          >
            <button>agregar articulo</button>
          </Link>
        </div>
        <Footer id="footer" />
      </div>
    );
  }
}

const mapStateToProps = (state, ownProps) => {
  return {
    boletines: state.boletines,
    matchedBoletin: state.boletines.find(
      bol => bol._id.toString() === ownProps.match.params.id
    ),
    boletinArticles: state.articles.filter(
      art => art.boletin.toString() === ownProps.match.params.id
    ),
    categories: state.categories,
    uploading_audio: state.uploading
  };
};

const mapDispatchToProps = dispatch => ({
  getArticles() {
    dispatch(fetchArticles());
  },
  getBoletines() {
    dispatch(fetchBoletines());
  },
  getCategories() {
    dispatch(fetchCategories());
  },
  deleteArticle(id) {
    dispatch(deleteArticle(id));
  },
  setUploading(bool) {
    dispatch(setUploading(bool));
  }
});

export default connect(mapStateToProps, mapDispatchToProps)(Boletin);



