import React, { Component } from "react";
import axios from "axios";
import Quill from "quill";
import { connect } from "react-redux";
import { Redirect } from "react-router";
import Toolbar from "../Toolbar/Toolbar";
import "./PostArticle.css";
import {
  postArticle,
  fetchCategories,
  fetchBoletines
} from "../actionCreators";
import triangle from "./triangle.png";
import Footer from "../Footer/Footer.js";

class Editor extends Component {
  state = {
    titulo: "",
    deltas: [],
    selectCat: false,
    catSelected: false,
    cat: 0,
    selectedCat: "",
    img: "",
    credit: ""
  };

  componentDidMount() {
    this.editors.push(
      new Quill("#editor", {
        theme: "snow",
        modules: {
          toolbar: [
            [{ header: [1, 2, false] }],
            ["bold", "italic", "underline"]
          ]
        }
      })
    );

    this.props.getCategories();

    this.props.getBoletines();
  }

  editors = [];

  handleNewEditor = () => {
    var contenido = document.getElementById("contenido");
    var nuevo = document.createElement("div");
    nuevo.className = "editor";
    nuevo.id = `editor${this.editors.length}`;

    contenido.appendChild(nuevo);

    this.editors.push(
      new Quill(`#editor${this.editors.length}`, {
        theme: "snow",
        modules: {
          toolbar: [
            [{ header: [1, 2, false] }],
            ["bold", "italic", "underline"]
          ]
        }
      })
    );
  };

  handleTitle = event => {
    this.setState({ title: event.target.value });
  };

  handleImage = event => {
    this.setState({
      img: event.target.value
    });
  };

  handleCredit = event => {
    this.setState({
      credit: event.target.value
    });
  };

  openCats = event => {
    this.setState({ selectCat: !this.state.selectCat });
  };

  selectCat = event => {
    this.setState({
      catSelected: true,
      selectCat: !this.state.selectCat,
      cat: event.target.id,
      selectedCat: event.target.value
    });
  };

  handleSubmit = event => {
    var deltas = this.editors.map(editor => {
      return JSON.stringify(editor.getContents());
    });
    this.setState(
      {
        deltas: deltas
      },
      () => {
        var reqBody = {
          deltas: this.state.deltas,
          title: this.state.title,
          boletin: this.props.match.params.boletin,
          category: this.state.cat,
          img: this.state.img,
          img_cred: this.state.credit
        };
        console.log(reqBody.deltas);
        this.props.postArticle(reqBody);
      }
    );
  };

  render() {
    if (this.props.success) {
      return <Redirect to={`/boletines/${this.props.boletin._id}`} />;
    }
    return (
      <div
        style={{
          minHeight: "100%",
          paddingBottom: 120,
          position: "relative"
        }}
      >
        <Toolbar activeTab={1} />
        <div className="content" id="contenido">
          <div className="section-t">
            Crear artículo para boletín{" "}
            {this.props.boletines.length > 0 ? (
              this.props.boletines.filter(
                bol => bol._id.toString() === this.props.match.params.boletin
              )[0].title
            ) : (
              ""
            )}
          </div>
          <div style={{ display: "flex", justifyContent: "space-between" }}>
            <input
              className="image-input"
              type="text"
              placeholder="Enlace a la imagen"
              onChange={this.handleImage}
            />
            <input
              className="image-input"
              type="text"
              placeholder="Autor de la foto"
              onChange={this.handleCredit}
            />
          </div>
          <input
            className="title-input"
            type="text"
            placeholder="Titular del articulo"
            onChange={this.handleTitle}
          />
          <div id="editor" className="editor" />
        </div>
        <div className="submit-row">
          <div className="cat-box">
            <button className="cat-select" onClick={this.openCats}>
              {this.state.catSelected ? (
                this.state.selectedCat
              ) : (
                "Seleccionar categoria"
              )}
              <img src={triangle} />
            </button>
            {this.state.selectCat && this.props.categories.length > 0 ? (
              this.props.categories.map(cats => {
                return (
                  <button
                    onClick={this.selectCat}
                    id={cats._id}
                    className="cat-row"
                    value={cats.title}
                  >
                    {cats.title}
                  </button>
                );
              })
            ) : this.state.selectCat ? (
              <p>No hay categorias</p>
            ) : (
              ""
            )}
          </div>
          <button className="add-new" onClick={this.handleNewEditor}>
            Agregar noticia
          </button>
          {this.state.catSelected ? (
            <button className="submit-article" onClick={this.handleSubmit}>
              PUBLICAR
            </button>
          ) : (
            <button className="no-submit" onClick={this.handleSubmit}>
              PUBLICAR
            </button>
          )}
        </div>
        <Footer />
      </div>
    );
  }
}

const mapDispatchToProps = dispatch => ({
  postArticle(reqBody) {
    dispatch(postArticle(reqBody));
  },
  getCategories() {
    dispatch(fetchCategories());
  },
  getBoletines() {
    dispatch(fetchBoletines());
  }
});

const mapStateToProps = (state, ownProps) => ({
  success: state.success,
  categories: state.categories,
  boletines: state.boletines,
  boletin: state.boletines.filter(
    bol => bol._id.toString() === ownProps.match.params.boletin
  )[0]
});

export default connect(mapStateToProps, mapDispatchToProps)(Editor);



