import React, { Component } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import Toolbar from "../Toolbar/Toolbar";
import trash from "../assets/trash.png";
import Delete from "../Common/Delete";
import Footer from "../Footer/Footer.js";

import { fetchCategories, deleteCategory } from "../actionCreators";
import "./Categorias.css";

const CategoriaRow = props => {
  return (
    <div className="boletin-row">
      <Link
        to="#"
        className="trash"
        onClick={props.delete(props.id, props.title)}
      >
        <img src={trash} />
      </Link>
      <div>
        <h2 style={{ borderBottom: `15px solid ${props.color}` }}>
          {props.title}
        </h2>
        <br />
      </div>
    </div>
  );
};

class Categorias extends Component {
  state = {
    delete: { bool: false }
  };
  componentDidMount() {
    this.props.getCategories();
  }

  deletePrompt = (id, title) => {
    return () => {
      this.setState({
        delete: {
          bool: true,
          id: id,
          title: title
        }
      });
    };
  };

  closeDialog = () => {
    this.setState({ delete: { bool: false } });
  };

  meto = id => {
    return () => {
      this.props.deleteCategory(id);
      this.setState({ delete: { bool: false } });
    };
  };

  render() {
    return (
      <div
        style={{ minHeight: "100%", paddingBottom: 120, position: "relative" }}
      >
        {this.state.delete.bool ? (
          <Delete
            kind="categoria"
            title={this.state.delete.title}
            borrar={this.meto}
            id={this.state.delete.id}
            closeDialog={this.closeDialog}
          />
        ) : null}
        <Toolbar activeTab={2} />
        <div
          className="section-title"
        
        >
          Categorías
        </div>
        <div className="category-container">
          {this.props.categorias.length > 0 ? (
            this.props.categorias.map(cat => (
              <CategoriaRow
                delete={this.deletePrompt}
                title={cat.title}
                color={cat.color}
                id={cat._id}
                key={cat._id}
              />
            ))
          ) : (
            <div
              style={{
                textAlign: "center",
                padding: "15%",
                fontSize: "2em",
                color: "#9B938B"
              }}
            >
              No hay categorías creadas
            </div>
          )}
        </div>
        <div className="add-categoria-container">
          <Link to="/categorias/post" className="add-categoria-btn">
            <button>agregar categoría</button>
          </Link>
        </div>
        <Footer />
      </div>
    );
  }
}

const mapStateToProps = state => ({
  categorias: state.categories
});

const mapDispatchToProps = dispatch => ({
  getCategories() {
    dispatch(fetchCategories());
  },
  deleteCategory(id) {
    dispatch(deleteCategory(id));
  }
});

export default connect(mapStateToProps, mapDispatchToProps)(Categorias);



