import React from "react";
import "./Footer.css";
import aldia from "../assets/aldia.jpg";
import analitica from "../assets/analitica.png";

const Footer = () => {
  return (
    <div className="admin-footer">
      <div>
        <img style={{ marginRight: 10 }} src={aldia} /> {"by"}{" "}
        <img style={{ marginLeft: 10 }} src={analitica} />
      </div>
    </div>
  );
};

export default Footer;



