import React, { Component } from "react";
import { connect } from "react-redux";
import { Redirect } from "react-router";
import Toolbar from "../Toolbar/Toolbar";
import Footer from "../Footer/Footer";

import { postCategory } from "../actionCreators";
import "./PostCategory.css";

class PostBoletin extends Component {
  state = {
    color: "",
    title: "",
    allowSubmit: false
  };

  checkInputs = () => {
    if (this.state.title.length > 0 && this.state.color.length > 0) {
      this.setState({
        allowSubmit: true
      });
    } else {
      this.setState({
        allowSubmit: false
      });
    }
  };

  colorInput = event => {
    this.setState(
      {
        color: event.target.value
      },
      () => {
        this.checkInputs();
      }
    );
  };

  titleInput = event => {
    this.setState(
      {
        title: event.target.value
      },
      () => {
        this.checkInputs();
      }
    );
  };

  submitCategory = () => {
    var reqBody = {
      title: this.state.title,
      color: this.state.color,
      main: false
    };

    if (this.state.title.toUpperCase() === "LA NOTICIA DEL DÍA") {
      reqBody.main = true;
    }

    this.props.createCategory(reqBody);
  };

  render() {
    console.log(`IMPORTANT PROP: ${this.props.success}`);
    if (this.props.success) {
      return <Redirect to="/categorias" />;
    }
    return (
      <div
        style={{ minHeight: "100%", paddingBottom: 120, position: "relative" }}>
        <Toolbar activeTab={2} />
        <div style={{ padding: "0px 15%" }}>
          <div className="route-tit">Crear categoría</div>
          <input
            type="text"
            className="category-input"
            placeholder="Nombre de la categoría"
            value={this.state.title}
            onChange={this.titleInput}
          />
          <span className="color-input-label">
            Color de la categoría: <br />
          </span>
          <input
            type="color"
            label="Eligeclor"
            className="category-input-color"
            value={this.state.color}
            onChange={this.colorInput}
            id="catColor"
          />
          <div>
            <button
              className={
                this.state.allowSubmit ? "submit-category" : "disabled-button"
              }
              onClick={this.state.allowSubmit ? this.submitCategory : null}
            >
              Crear
            </button>
          </div>
        </div>
        <Footer />
      </div>
    );
  }
}

const mapStateToProps = state => ({
  success: state.success,
  categories: state.categories
});

const mapDispatchToProps = dispatch => ({
  createCategory(reqBody) {
    dispatch(postCategory(reqBody));
  }
});

export default connect(mapStateToProps, mapDispatchToProps)(PostBoletin);



