import axios from "axios";
import {
  GET_BOLETINES,
  GET_ARTICLES,
  GET_CATEGORIES,
  CREATE_BOLETIN,
  CREATE_ARTICLE,
  CREATE_CATEGORY,
  SUCCESS,
  UPLOAD_AUDIO
} from "./actions";

export function setSuccess(bool) {
  return { type: SUCCESS, payload: bool };
}

export function setUploading(bool) {
  return { type: UPLOAD_AUDIO, payload: bool };
}

export function getBoletines(boletines) {
  return { type: GET_BOLETINES, payload: boletines };
}

export function getArticles(articles) {
  return { type: GET_ARTICLES, payload: articles };
}

export function createArticle(article) {
  return { type: CREATE_ARTICLE, payload: article };
}

export function getCategories(categories) {
  return { type: GET_CATEGORIES, payload: categories };
}

export function fetchBoletines() {
  return dispatch => {
    axios.get("https://boletines.online/api/boletin").then(resp => {
      if (typeof resp.data === "string") {
        dispatch(getBoletines([]));
      } else {
        resp.data.sort(function(a, b) {
          if (a.created_at > b.created_at) {
            return -1;
          } else if (a.created_at < b.created_at) {
            return 1;
          } else {
            return 0;
          }
        });

        dispatch(getBoletines(resp.data));
      }
    });
  };
}

export function fetchArticles() {
  return dispatch => {
    axios.get("https://boletines.online/api/article").then(resp => {
      if (typeof resp.data === "string") {
        dispatch(getArticles([]));
      } else {
        dispatch(getArticles(resp.data));
      }
    });
  };
}

export function fetchCategories() {
  return dispatch => {
    axios.get("https://boletines.online/api/category").then(resp => {
      if (typeof resp.data === "string") {
        dispatch(getCategories([]));
      } else {
        dispatch(getCategories(resp.data));
      }
    });
  };
}

export function postArticle(reqBody) {
  return dispatch => {
    console.log(reqBody.deltas);
    axios
      .post(
        "https://boletines.online/api/article",
        `deltas=${JSON.stringify(
          reqBody.deltas
        )}&title=${reqBody.title}&boletin=${reqBody.boletin}&category=${reqBody.category}&img=${reqBody.img}&img_cred=${reqBody.img_cred}`
      )
      .then(resp => {
        dispatch(setSuccess(true));

        function rev() {
          dispatch(setSuccess(false));
        }
        window.setTimeout(rev, 1000);
      });
  };
}

export function postCategory(reqBody) {
  return dispatch => {
    axios
      .post(
        "https://boletines.online/api/category",
        `color=${reqBody.color}&title=${reqBody.title}&main=${reqBody.main}`
      )
      .then(resp => {
        dispatch(setSuccess(true));

        function rev() {
          dispatch(setSuccess(false));
        }

        window.setTimeout(rev, 1000);
      });
  };
}

export function postBoletin(reqBody) {
  return dispatch => {
    axios
      .post(
        "https://boletines.online/api/boletin",
        `title=${reqBody.title}&message=${reqBody.msg}`
      )
      .then(resp => {
        dispatch(setSuccess(true));
        function rev() {
          dispatch(setSuccess(false));
        }
        window.setTimeout(rev, 1000);
      });
  };
}

export function deleteBoletin(id) {
  return dispatch => {
    axios.delete(`https://boletines.online/api/boletin/${id}`).then(resp => {
      dispatch(fetchBoletines());
    });
  };
}

export function deleteCategory(id) {
  return dispatch => {
    axios.delete(`https://boletines.online/api/category/${id}`).then(resp => {
      dispatch(fetchCategories());
    });
  };
}

export function deleteArticle(id) {
  return dispatch => {
    axios.delete(`https://boletines.online/api/article/${id}`).then(resp => {
      dispatch(fetchArticles());
    });
  };
}

export function putBoletin(id) {
  return dispatch => {
    axios
      .put(`https://boletines.online/api/boletin/${id}`, `published=${true}`)
      .then(resp => {
        dispatch(fetchBoletines());
      });
  };
}



