import React, { Component } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { fetchArticles, fetchCategories } from "../actionCreators";
import "./Article.css";

class Article extends Component {
  componentDidMount() {
    this.props.getArticles();
    this.props.getCategories();
  }

  render() {
    var cat = this.props.matchedArticle
      ? this.props.matchedArticle.category
      : "0";
    var catObj;
    if (!catObj) {
      catObj = { title: "Sin categoria", color: "#000", id: 0 };
    } else {
      catObj = this.props.categories.filter(
        cate => cate._id.toString() === cat
      )[0];
    }

    var Content = () => {
      if (this.props.matchedArticle) {
        return (
          <div
            dangerouslySetInnerHTML={{ __html: this.props.matchedArticle.html }}
          />
        );
      } else {
        return "";
      }
    };

    return (
      <div style={{ padding: "50px 15%" }}>
        <div
          style={{
            backgroundColor: `${catObj.color}` || "#000",
            color: "#FFF",
            padding: "10px 10px"
          }}
        >
          {catObj.title}
        </div>
        <h1>
          {this.props.matchedArticle ? this.props.matchedArticle.title : ""}
        </h1>
        {this.props.matchedArticle ? this.props.matchedArticle.intro : ""}
        <p />
        <div>
          <Content />
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state, ownProps) => ({
  articles: state.articles,
  matchedArticle: state.articles.filter(
    art => art._id.toString() === ownProps.match.params.id
  )[0],
  categories: state.categories
});

const mapDispatchToProps = dispatch => ({
  getArticles() {
    dispatch(fetchArticles());
  },
  getCategories() {
    dispatch(fetchCategories());
  }
});
export default connect(mapStateToProps, mapDispatchToProps)(Article);



